
.form-container {
    background-color: #f9f9f9; 
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
    width: 300px; 
    margin: auto; 
    margin-top: 100px;
  }
  
  /* Form group */
  .form-group {
    margin-bottom: 15px; 
  }
  
  /* Labels */
  label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  
  .input-field {
    width: calc(100% - 20px); 
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
  }
  
  textarea.input-field {
    height: 80px;
  }
  
  /* Submit button */
  .submit-button {
    background-color: #4CAF50; 
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    width: 100%; 
  }
  
  .submit-button:hover {
    background-color: #45a049;
  }
  
  /* Heading */
  .form-heading {
    text-align: center;
    font-family: 'Roboto', sans-serif;
    font-size: 24px;
    margin-bottom: 20px;
  }

  /* Media queries */
  @media screen and (min-width: 601px) and (max-width: 1024px) {
    
    .form-container {
      width: 70%; 
  }
  
  @media screen and (min-width: 1025px) {
    /* Large devices */
    .form-container {
      width: 50%; 
    }
  }}